body {
  justify-content: center;
  height: 100vh;
  margin: 0;
  display: flex;
  overflow: hidden;
}

.container {
  color: #2f4f4f;
  max-width: 100%;
  margin-top: 30px;
  font-family: Arial, Helvetica, sans-serif;
}

.container__people {
  box-sizing: border-box;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: grid;
}

.container__person {
  text-align: center;
  transform-origin: center;
  flex-direction: column;
  align-items: center;
  width: 231px;
  transition: transform 1s 1s;
  display: flex;
}

.container__person:hover {
  background-color: #eee;
}

.container__person:hover .container__photo {
  display: all;
  border-radius: 25%;
  transition: transform 1s 1s, border-radius 1s 1s;
  transform: scale(1.2);
}

.container__person:hover .details__extra {
  opacity: 1;
  margin-top: 10px;
  transition: opacity 1s;
}

.container__photo {
  box-sizing: border-box;
  object-fit: cover;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  position: relative;
  overflow: hidden;
}

.container img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform 1s;
  position: relative;
}

.container img .rank {
  color: #000;
  background-color: #fff;
  border-radius: 10px;
  font-size: 20px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.container .name {
  color: #2f4f4f;
  font-size: 20px;
}

.container .details {
  flex-direction: column;
  align-items: center;
  margin-top: 5px;
  display: flex;
}

.container .details__extra {
  opacity: 0;
}

.container .details__extra:hover {
  display: block;
}

@media (width <= 750px) and (width >= 500px) {
  .container__people {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 1000px) and (width <= 1500px) {
  .container__people {
    grid-template-columns: repeat(4, 1fr);
  }
}
